<template>
  <v-card>
    <div>
      <h1>This is the Home page</h1>
    </div>


    <v-container>
      <h1>Stocklist</h1>
        
      <v-row no-gutters>
        <v-col cols="12">
          <v-autocomplete
            v-model="select"
            :items="stocklist"
            :loading="loading"
            :search-input.sync="search"
            dense
            filled
            cache-items
            label="Search the StockList"
            item-text="symbol"
            item-value="symbol"
            @keyup.enter="goToStockLookup(select)"
          ></v-autocomplete>
          <template v-slot:item="data">
                <template>
                  <v-list-item-content>
                    <v-list-item-title  @click="goToStockLookup(data.item)">{{ data.item }}</v-list-item-title>
                  </v-list-item-content>
                </template>
              </template>
        </v-col>
        <v-col
          v-for="(stock, i) in stocklist"
          :key="i"
          cols="3"
          xs="2"
          sm="2"
          md="2"
          lg="2"
        >
          <v-card
            class="pa-2"
            outlined
            tile
            @click="goToStockLookup(stock)"
          >
            {{ stock }}
          </v-card>
        </v-col>
      </v-row>

    </v-container>
  </v-card>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Home',
  data: () => ({
    loading: false,
    items: [],
    search: null,
    select: null,
    stocklist: ['VXX', 'QQQ', 'SPY', 'AAL', 'AAPL', 'ABNB', 'ACB', 'AIG', 'AMD', 'ATVI', 'BA', 'BABA', 'BAC', 'BBY', 'BIDU', 'BYND', 'C', 'CCL', 'CHWY', 'COST', 'CRM', 'CSCO', 'CVS', 'DAL', 'DBX', 'DIS', 'DISH', 'DKNG', 'EA', 'EBAY', 'F', 'FB', 'FDX', 'FSLY', 'GE', 'GM', 'GME', 'GPS', 'HD', 'IBM', 'INTC', 'JBLU', 'JNJ', 'JPM', 'KNDI', 'LAZR', 'LLY', 'LULU', 'LYFT', 'M', 'MA', 'MCD', 'MGM', 'MSFT', 'MU', 'NFLX', 'NIO', 'NKE', 'NVDA', 'ORCL', 'OSTK', 'PENN', 'PINS', 'PLTR', 'PLUG', 'PTON', 'PYPL', 'QCOM', 'RKT', 'ROKU', 'SBUX', 'SHOP', 'SIRI', 'SNAP', 'SNOW', 'SPCE', 'SQ', 'T', 'TGT', 'TLRY', 'TSLA', 'TWTR', 'UAL', 'UBER', 'V', 'VZ', 'WBA', 'WFC', 'WMT', 'WORK', 'ZM', 'ZNGA']
  }),
   watch: {
      search (val) {
        let item = val.toUpperCase()
        if (item.length < 3) {
          this.loading = false     
        } else if (item.length > 3) {
          if (item.length == 3 && item in this.stocklist) {
            this.loading = true
            this.items = []
            this.select = null
            this.goToStockLookup(item)
          } else {
            this.loading = false
          }
        } else {
          this.loading = false          
        }
      },
    },
  
  created() {
    this.$store.dispatch('getTimeframes')
  },
  methods: {
    goToStockLookup(stock){
      this.$router.push({ name: 'Stock', params: { id: stock } })
    },
   
  },
  computed: {
    ...mapGetters(['timeframes']),

  },
}
</script>

